export const camelToSnakeCase = (str) => {
  return str.replace(/[A-Z]/g, (letter) => `_${letter.toLowerCase()}`);
};

export const toSentenceCase = (str) => convertCase(str, "sentence");
export const toTitleCase = (str) => convertCase(str, "title");
export const toKebabCase = (str) => convertCase(str, "kebab");
export const toPascalCase = (str) => convertCase(str, "pascal");
export const toCamelCase = (str) => convertCase(str, "camel");
export const toSnakeCase = (str) => convertCase(str, "snake");

export const convertCase = (str, toCase = "camel") => {
  if (!str) return "";

  const delimiter =
    toCase === "snake"
      ? "_"
      : toCase === "kebab"
        ? "-"
        : ["title", "sentence"].includes(toCase)
          ? " "
          : "";

  const transform = ["camel", "pascal"].includes(toCase)
    ? (x) => x.slice(0, 1).toUpperCase() + x.slice(1).toLowerCase()
    : ["snake", "kebab"].includes(toCase)
      ? (x) => x.toLowerCase()
      : toCase === "title"
        ? (x) => x.slice(0, 1).toUpperCase() + x.slice(1)
        : toCase === "sentence"
          ? (x) => x.toLowerCase()
          : (x) => x;

  const finalTransform =
    toCase === "camel"
      ? (x) => x.slice(0, 1).toLowerCase() + x.slice(1)
      : toCase === "sentence"
        ? (x) => x.slice(0, 1).toUpperCase() + x.slice(1)
        : (x) => x;

  const words = str.match(
    /[A-Z]{2,}(?=[A-Z][a-z]+[0-9]*|\b)|[A-Z]?[a-z]+[0-9]*|[A-Z]|[0-9]+/g,
  );

  return finalTransform(words.map(transform).join(delimiter));
};
